import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { menumodel } from '../core/Models/menumodel';

@Component({
  selector: 'app-banks-menu',
  templateUrl: './banks-menu.component.html',
  styleUrls: ['./banks-menu.component.css']
})
export class BanksMenuComponent implements OnInit {
  entries: menumodel[] = [];
  constructor(private router: Router) { }
  MenuHandler(m: menumodel)
  {
    const NavArray = [];
    NavArray.push(m.route);
    this.router.navigate(NavArray);
  }
  ngOnInit(): void {
    let m = new menumodel();
    m.fa_icon = 'fa fa-list';
    m.title = 'My Reservations';
    m.route = 'wrapper/list';
    this.entries.push(m);

    m = new menumodel();
    m.fa_icon = 'fa fa-ticket';
    m.title = 'New Reservation';
    m.route = 'wrapper/banks-list';
    this.entries.push(m);
  }

}
